.calculator {
  position: relative;
  margin: 10rem auto;
  min-height: 400px;
  max-width: 85%;
  width: 400px;
  box-shadow: -40px 20px 20px rgba(0, 0, 0, 0.226);
  /* border-radius: 20px; */
  /* transform: skewX(-2deg); */
  border: 2px solid black;
  display: grid;
  padding: 2rem;
  gap: 10px;
  grid-template-columns: repeat(4, minmax(0, 1fr));
  grid-template-rows: 100px repeat(5, minmax(0, 1fr));
  z-index: 100;
}
.calculator::before {
  position: absolute;
  content: "";
  width: 10px;
  height: 100%;
  border: 2px solid black;
  top: -1.5px;
  left: -15px;
  background-color: white;
  transform-origin: right;
  transform: skewY(45deg);
  z-index: 50;
}
.calculator::after {
  position: absolute;
  content: "";
  width: 100%;
  height: 10px;
  border: 2px solid black;
  top: -15px;
  left: -1px;
  background-color: white;
  transform-origin: bottom;
  transform: skewX(45deg);
  z-index: 50;
}
