.button {
  font-size: clamp(0.8rem, 6vw, 2rem);
  padding: 0;
  cursor: pointer;
  text-align: center;
  min-height: 0;
  min-width: 0;
  background-color: white;
}
.button:hover {
  background-color: rgba(0, 0, 0, 0.3);
}
.button:focus {
  background-color: black;
  color: white;
}
